import React from 'react';
import { useSchema } from '../providers/SchemaContext';
import { ComputedQuery } from '@wix/patterns';

interface UseFetchDataProps {
  searchableFieldIds?: string[];
  filterFieldMapping?: Record<string, { fieldId: string }>;
}

export const useFetchData = ({
  searchableFieldIds,
  filterFieldMapping,
}: UseFetchDataProps) => {
  const schema = useSchema();

  const fetchData = React.useMemo(() => {
    return (query: ComputedQuery<any>) => {
      return schema.actions.find(query, {
        searchableFieldIds: searchableFieldIds || [],
        filterFieldMapping,
      });
    };
  }, [schema, searchableFieldIds, filterFieldMapping]);

  return {
    fetchData,
  };
};
